@import url('https://fonts.googleapis.com/css2?family=Amiri:wght@400;700&family=Nunito:wght@200;300;400;500&display=swap');

:root {
  --font-primary: 'Nunito', sans-serif;
  --font-heading: 'Amiri', sans-serif;
  --color-primary: #FA4529;
  --color-primary: #ef2b23;
  --color-primary-light: #EDDAD8;  
  --color-grey-dark: #575757;
  --color-grey: #999999;
  --color-grey-light: #D9D9D9;
  --color-grey-lighter: #EBEBEB;
  --color-grey-lighter-2: #f0f0f0;
  --color-grey-lighter-3: #e4e4e4;
}

html {
  font-size: 62.5%; 
  scroll-snap-type: y mandatory
}
