:root {
  --font-primary: 'Nunito', sans-serif;
  --font-heading: 'Amiri', sans-serif;
  --color-primary: #FA4529;
  --color-primary-light: #EDDAD8;  
  --color-grey-dark: #575757;
  --color-grey: #999999;
  --color-grey-light: #D9D9D9;
  --color-grey-lighter: #EBEBEB;
  --color-grey-lighter-2: #f0f0f0;
  --color-grey-lighter-3: whitesmoke;

  --height-header: 7rem;
}

.App {
  text-align: center;
}
.App-logo {
  height: var(--height-header);
  margin-top: 4rem;
}
.App-logo:hover {
  cursor: pointer;
}
.header-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: var(--height-header);
  width: 100%;
  padding: 3rem 5rem;
  position: fixed;
  background-color: transparent;
  z-index: 50;
}
.header-links {
  display: flex;
  width: 14rem;
  justify-content: space-between;
  position: relative;
}
.link-text {
  font-size: 1.6rem;
  font-weight: 400;
  color: var(--color-grey);
  width: 6rem;
  cursor: pointer;
}
.link-text:hover {
  color: var(--color-primary); 
}
.dot1,.dot2  {
  height: .8rem;
  width: .8rem;
  border-radius: 1rem;
  transition: all .7s;
}
.dot1 {
  position: absolute;
  top: 3.2rem;
  left: 2.6rem;
}
.dot2 {
  position: absolute;
  top: 3.2rem;
  right: 2.6rem;
}
.active-text {
  color: var(--color-primary); 
  font-weight: 500;
}
.active-dot {
  background-color: var(--color-primary); 
}

.header-design {
  width: 3.5rem;
  height: var(--height-header);
  background-color: var(--color-primary-light);
}
.header-design-links {
  opacity: 0;
  background-color: var(--color-primary-light);
  width: 100%;
  height: 100%;
  background-color: inherit;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  cursor: pointer;
  transition: all .6s ease-in-out;
}
.header-design-links:hover {
  height: calc(2.2 *var(--height-header));
  opacity: 1;
  border-bottom: 4px solid var(--color-primary);
}
.flex-row {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.flex-col {
  display: flex;
  flex-direction: column;
}


.sections {
  width: 100%;
}
.sec {
  scroll-snap-align: start;
  width: 100%;
}
.sec:nth-child(odd) {
  background-color: var(--color-grey-lighter-3);;
  }
  .sec:nth-child(even) {
    background-color: var(--color-grey-lighter-2);;
  }

.section {
  height: 100vh;
  width: 100%;
  padding: 5rem 0;
  max-width: 144rem;
  display: grid;
  align-items: center;
  margin: 0 auto;
}
.intro-row-1 {
  width: 44rem;
  height: 16rem;
  text-align: right;
}
.intro-text-1 {
  font-family: 'Amiri', 'sans-serif';
  font-weight: 700;
  font-size: 12.5rem;
  color: var(--color-grey-dark);
  position: relative;
  z-index: 10;
  text-shadow: 1px 2px 5px var(--color-grey-lighter-3);
}
.intro-text-1::after {
  content: "";
  height: 50rem;
  width: 2px;
  background-color: var(--color-primary);
  position: absolute;
  left: 60rem;
  transform: rotate(20deg);
}
.intro-row-2 {
  text-align: right;
  justify-content: space-between;
}
.iam {
 font-size: 5rem;
 font-family: 'Amiri', 'sans-serif';
 font-weight: 400;
 width: 44rem;
 height: 16rem;
 color: var(--color-grey-dark);
 position: relative;
 z-index: 2;
 text-shadow: 1px 2px 5px var(--color-grey-lighter-3);
}
.iam::before {
  content: "";
  width: 40rem;
  height: 30rem;
  background-color: var(--color-grey-light);
  position: absolute;
  right: 7rem;
  top: -6rem;
  z-index: -5;
}
.iam::after {
  content: "";
  width: 2rem;
  height: 10rem;
  background-color: var(--color-primary-light);
  position: absolute;
  left: 46rem;
  top: 1rem;
  z-index: -5;
}

.parminder {
  font-size: 4rem;
  font-family: 'Amiri', 'sans-serif';
  font-weight: 400;
  width: 50rem;
  height: 10rem;
  color: var(--color-grey-dark);
  position: relative;
  margin-right: 7rem;
  text-shadow: 1px 2px 5px var(--color-grey-lighter-3);
}
.parminder::after {
  content: "";
  width: 86%;
  height: 4rem;
  background-color: var(--color-grey-light);
  position: absolute;
  left: 9rem;
  bottom: 1rem;
  z-index: -5;
}

.btn-outlined {
  height: 6rem;
  width: 20rem;
  border: 2px solid var(--color-primary);
  background-color: transparent;
  color: var(--color-primary);
  font-size: 1.8rem;
  font-weight: 400;
  margin-left: 40%;
  transition: all .3s;
}
.btn-outlined:hover {
  background-color: var(--color-primary);
  color: var(--color-grey-lighter);
  cursor: pointer;
}

.social-box {
  display: flex;
  width: 80rem;
  justify-content: flex-end;
  margin: 4rem auto;
}
.connect {
   font-size: 1.6rem;
   width: 5rem;
   position: relative;  
   margin-right: 5rem;
}
.connect::after {
  content: "";
  height: 5rem;
  width: 2px;
  background-color: var(--color-primary);
  position: absolute;
  right: -3rem;
  top: -1rem;
  transform: rotate(20deg);
}
.mx5 {
  margin: .4rem .7rem;
  transition: transform .2s ease-in-out;
}
.mx5:hover {
  transform: scale(1.15);
}

.section-content {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.heading-box {
  height: 8rem;
  width: fit-content;
  text-align: left;
  padding: 2rem 14rem;
}
.heading {
  font-size: 3rem;
  font-family: 'Amiri', 'sans-serif';
  color: var(--color-grey-dark);
  position: relative;
  z-index: 10;
}
.heading::before {
  content: "";
  width: 2px;
  height: 4rem;
  background-color: red;
  color: red;
  position: absolute;
  left: -1.2rem;
  bottom: 1rem;
  transform: rotate(10deg);
  z-index: 0;
}
.heading::after {
  content: "";
  width: 95%;
  height: 2rem;
  background-color: var(--color-grey-light);
  position: absolute;
  left: 2rem;
  bottom: 1rem;
  z-index: -1;
}



.sidebar-box {
  position: fixed;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  height: 10rem;
  width: 4rem;
  z-index: 30;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.sbar {
  background-color: var(--color-grey-light);
  width: .6rem;
  height: 1.2rem;
  border-radius: .6rem;
}
.sbar-active {
  background-color: var(--color-primary);
  width: .8rem;
  height: .8rem;
}